.final-result{
    font-size: 200px;
    position: absolute;
    width: 100%;
    height: 100%;
    text-align: center;
    padding-top: 60px;
    background-color: rgba(214, 214, 214, 0.568)  ;
}
.restart-id-container{
    position: absolute;
    bottom: 0;
    width: 100%;
}
.restart-id{
    width: 100%;
    position: absolute;
    left: 0;
    bottom: 0;
}
*{
    box-sizing: border-box;
    font-family: 'Source Sans Pro', sans-serif;
}

.button{
    padding: 16px 32px;
    outline: none;
    border: none;
   background-color: grey; 
   transition-property: background,color;
   transition-duration: 0.25s;
   color: white;
}
.button:disabled{
    background: rgb(235, 235, 235);
    color: rgb(151, 151, 151);
}
.button:not(:disabled):hover{
    background-color: rgb(224, 224, 224); 
    color: black;
}
.square{
    width: 200px;
    height: 200px;
    display: inline-block;
   margin: 32px auto;
   border-radius: 16px;
   transition-duration: 0.25s;
   transition-property: border;
}
 .square:hover{
    border: 4px solid rgb(0, 0, 0);

} 
.squares{
    display: grid;
    grid-template-columns: auto auto auto;
    

}
.board{
    height: 600px;
    background-color: rgb(224, 224, 224);
    position: relative;
}
.submit-btn-container{
    position: absolute;
    bottom: 0;
    display: flex;
    width: 100%;
    justify-content: center;
}
.submit-btn{
    width: 100%;

}

.radio-btn{
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    display: inline-block;
    position: relative;
    background-color: #f1f1f1;
    color: #666;
    top: 10px;
    height: 30px;
    width: 30px;
    border: 0;
    border-radius: 50px;
    cursor: pointer;     
    margin: 0;
    margin-right: 8px;
    
    outline: none;
}

.radio-btn:checked::before
{
     position: absolute;
     font: 13px/1 'Open Sans', sans-serif;
     left: 11px;
     top: 7px;
     content: '\2143';
     -webkit-transform: rotate(40deg);
             transform: rotate(40deg);
}


